import { make } from 'vuex-pathify'
// import { config } from '@/plugins'
// import axios from 'axios'

import { IN_BROWSER } from '@/util/globals'

// const http = axios.create({
//   headers: {
//     'Content-Type': 'application/json',
//   },
// })

// const api = config.baseApi + '/api/v1/contacts'

const state = {
  defaultView: "card",
  defaultSort: "department_title",
  defaultGroup: "company_department",
  loadAllContacts: false,
  searchIntercept: true,
  searchAutoHide: false,
  joinDepartmentName: true,
  clickDepartmentName: true,
  clickDepartmentNameLast: true,
  clipboardFastCopy: false,
  clipboardCopyItems_name: true,
  clipboardCopyItems_title: true,
  clipboardCopyItems_company: true,
  clipboardCopyItems_department: true,
  mainMenu: false,
}

const mutations = make.mutations(state)

const actions = {
  // ...make.actions(state),
  fetch: ({ state, commit }) => {
    const local = localStorage.getItem('phonebook@app') || '{}'
    const app = JSON.parse(local)

    for (const key in state) {
      if (!(app[key] === undefined)) {
        let value = app[key]
        if (typeof state[key] === "object") {
          value = Object.assign(state[key], app[key])
        }
        commit(key, value)
      }
    }
  },
  update: ({ state }) => {
    if (!IN_BROWSER) return

    localStorage.setItem('phonebook@app', JSON.stringify(state))
  },
}

const getters = {}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
}