// Pathify
import { make } from 'vuex-pathify'
import axios from 'axios'

const http = axios.create({
  headers: {
    'Content-Type': 'application/html',
  },
})

const api = '/api/v1/help'

const state = {
  help: "",
}

const mutations = make.mutations(state)

const actions = {
  loadHelp: ({ commit }) => {
    http.get(api).then(res => commit('help', res.data))
  },
}

const getters = {}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
}