const version = require('../../package').version
let baseVersion = version.split('.').slice(0,2).join('.')

const baseApi = ''
// const baseApi = 'http://10.10.2.250:3000'
// const baseApi = 'https://phonebook.adm.tver.ru'

export default {
  title: (title) => {
    let baseTitle = "Телефонный справочник [версия " + baseVersion + "]"
    if (title) {
      return title + " | " + baseTitle
    }
    return baseTitle
  },
  version,
  baseVersion,
  baseApi,
}