import { createStore } from 'vuex'
import pathify from '@/plugins/vuex-pathify'

// Modules
import * as modules from './modules'

const store = createStore({
  modules,
  plugins: [
    pathify.plugin,
  ],
})

store.subscribe(mutation => {
  if (mutation.type.startsWith('app/'))
    store.dispatch('app/update', mutation)

  if (mutation.type === 'phonebook/filter')
    store.dispatch('phonebook/filterChange')
})

store.dispatch('app/fetch')
store.dispatch('phonebook/loadContacts', { limit: 10, from: 0 })
store.dispatch('phonebook/registerHashChange')

export default store

