// Pathify
import { make } from 'vuex-pathify'
import { config } from '@/plugins'
import axios from 'axios'
import _ from 'lodash'

const http = axios.create({
  headers: {
    'Content-Type': 'application/json',
  },
})

const api = config.baseApi + '/api/v1/contacts'

const loadContacts = ({ commit, state }) => {
  if (!state.allDataLoaded) {
    const limit = state.from == 0 ? 10 : state.limit
    commit('loadingShow', state.from > 0)
    commit('loading', true)
    http.get(api, { params: { from: state.from, limit, q: state.filter } }).then((res) => {
      commit('apiService', true)
      commit('contact', res.data.contact)
      commit('contacts', state.from == 0 ? res.data.contacts || [] : [...state.contacts, ...res.data.contacts])
      commit('from', state.contacts.length)
      commit('allDataLoaded', (res.data.contacts && res.data.contacts.length) < limit)
    }).finally(() => {
      commit('loading', false)
      commit('loadingShow', false)
    }).catch(() => {
      commit('apiService', false)
    })
  }
}
const debounceLoadContacts = _.debounce(loadContacts, 500)
const getUrlHashValue = () => {
  let q = decodeURIComponent(window.location.hash.slice(1))
  switch (q) {
    case "" || "index":
      return ""
  }
  return q
}

const state = {
  filter: getUrlHashValue(),
  from: 0,
  limit: 30,
  contact: null,
  contacts: [],
  loading: true,
  loadingShow: false,
  allDataLoaded: false,
  apiService: true,
}

const mutations = make.mutations(state)

const actions = {
  loadContacts: loadContacts,
  filterChange: (context, filter) => {
    window.location.hash = context.state.filter
    if (filter == context.state.filter || context.state.loading)
      return
    context.commit('allDataLoaded', false)
    // context.commit('contact', null)
    // context.commit('contacts', [])
    context.commit('from', 0)
    context.commit('loading', true)
    context.commit('loadingShow', false)
    if (filter != undefined) {
      // console.log('fast')
      context.commit('filter', filter)
      debounceLoadContacts.cancel()
      loadContacts(context)
    } else {
      // console.log('debounce')
      debounceLoadContacts(context)
    }
  },
  registerHashChange: ({ commit }) => {
    window.onhashchange = () => {
      let q = getUrlHashValue()
      switch (q) {
        case "" || "index":
          commit('filter', '')
          break
        default:
          commit('filter', q)
      }
    }
  },
}

const getters = {}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
}